<script>
  let isDarkThemeChecked = false;
  function toggleTheme() {
    isDarkThemeChecked = !isDarkThemeChecked;
    document.documentElement.classList.toggle('dark__theme');
  }
</script>

<svelte:body class:dark__theme={isDarkThemeChecked} />


<div class="react-toggle" on:click={toggleTheme}>
  <div class="react-toggle-track">
    <img
      src="data:image/png;base64,"
      role="presentation" style="pointer-events: none;" width="16" height="16" alt="moon">
    <img
      src="data:image/png;base64,"
      role="presentation" style="pointer-events: none;" width="16" height="16" alt="star">
  </div>
  <div class="react-toggle-thumb" class:checked={isDarkThemeChecked}></div>
</div>

<style>
  .react-toggle {
    position: relative;
    cursor: pointer;
  }

  .react-toggle-track {
    display: flex;
    width: 50px;
    height: 24px;
    align-items: center;
    justify-content: space-between;
    background-color: #0f1114;
    padding: 3px;
    border-radius: 12px;
  }

  .react-toggle-thumb {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    left: 2px;
    top: 2px;
    transition: 130ms linear;
  }

  .react-toggle-thumb.checked {
    left: calc(100% - 2px);
    transform: translate(-100%, 0);
  }
</style>